export default class UnderBlock {
    constructor(el,selector){
        this.root = el
        this.selector = selector
        if(!this.root || !this.selector) return false
        this.block = this.root.closest(`[${this.selector}-container]`)
        this._init();
    }

    _init(){
        this._eventHandler();
    }
    _eventHandler(){
        this.root.addEventListener('click', ()=>{
            let header = document.querySelector('#header'),
            scrollTop = this.block.getBoundingClientRect().bottom + window.pageYOffset
            window.scrollTo({
                top: scrollTop - header.offsetHeight,
                behavior: 'smooth'
            });
        })
    }
}